@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@500;700;900&display=swap");
*::-webkit-input-placeholder {
  color: #202020;
  opacity: 1;
}

*::-moz-placeholder {
  color: #202020;
  opacity: 1;
}

*::-ms-input-placeholder {
  color: #202020;
  opacity: 1;
}

::placeholder {
  color: #202020;
}

::selection {
  background-color: orange;
  color: #fff;
}

/* font-family: "OpenSansRegular"; */
@font-face {
  font-family: "Montserrat-Regular";
  src: url("../fonts/Montserrat/Montserrat-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat/Montserrat-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
  font-display: fallback;
}

@font-face {
  font-family: "Montserrat-Bold";
  src: url("../fonts/Montserrat-Bold/Montserrat-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat-Bold/Montserrat-Bold.woff") format("woff"), url("../fonts/Montserrat-Bold/Montserrat-Bold.ttf") format("truetype");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}

@font-face {
  font-family: "Montserrat-SemiBold";
  src: url("../fonts/Montserrat-SemiBold/Montserrat-SemiBold.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat-SemiBold/Montserrat-SemiBold.woff") format("woff"), url("../fonts/Montserrat-SemiBold/Montserrat-SemiBold.ttf") format("truetype");
  font-style: normal;
  font-weight: 600;
  font-display: swap;
}

@font-face {
  font-family: "Montserrat-Medium";
  src: url("../fonts/Montserrat-Medium/Montserrat-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat-Medium/Montserrat-Medium.woff") format("woff"), url("../fonts/Montserrat-Medium/Montserrat-Medium.ttf") format("truetype");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}

html {
  width: 100%;
  /* height: 100%; */
  font-size: 16px;
  line-height: 1.42;
  margin: 0;
  padding: 0;
  -webkit-backface-visibility: hidden;
  /* overflow-x: hidden; */
}

body {
  font-size: 16px;
  min-width: 320px;
  opacity: 1;
  position: relative;
  line-height: 1.42;
  font-family: "Montserrat-Regular", sans-serif;
  overflow-x: hidden;
  color: #000000;
}

body input, body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid, body textarea:required:valid {
  border-color: green;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

.container {
  max-width: 1100px;
  margin: auto;
  width: 100%;
}

.aside_left {
  width: 270px;
  background-color: #fdf9ff;
  position: fixed;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 10;
  border-right: 1px solid #e8dfec;
  padding: 30px 20px;
}

.main-content {
  padding-left: 270px;
}

.section {
  background-color: #f2f2fc;
  min-height: 100vh;
  padding: 0 30px;
}

.logo {
  padding: 0;
}

.logo a {
  font-size: 40px;
  color: #302e4d;
  font-family: 'Rubik', sans-serif;
  font-weight: 700;
  text-decoration: none;
  display: inline-block;
  position: relative;
  padding: 0 10px;
}

.logo a:before,
.logo a:after {
  content: '';
  height: 20px;
  width: 20px;
  position: absolute;
}

.logo a:before {
  top: 0;
  left: 0;
}

.logo a:after {
  bottom: 0;
  right: 0;
}

.aside_left .nav {
  margin: 80px 0;
  list-style: none;
}

.aside_left .nav li {
  display: block;
}

.aside_left .nav li a {
  display: block;
  font-size: 16px;
  font-weight: 600;
  color: #302e4d;
  text-decoration: none;
  line-height: 45px;
  border-bottom: 1px solid #e8dfec;
  transition: all .3s ease;
}

.aside_left .nav li a:not(.active):hover {
  padding-left: 5px;
}

.copyright_text {
  color: #7d7d7d;
  font-size: 13px;
}

.aside_left .nav li a .fa {
  margin-right: 5px;
}

.aside_left .nav-toggler {
  width: 40px;
  height: 40px;
  border: 1px solid #d4d4e3;
  position: fixed;
  left: 300px;
  top: 30px;
  z-index: 11;
  cursor: pointer;
  background-color: #fdf9ff;
  display: flex;
  align-items: center;
  justify-content: center;
  display: none;
}

.aside_left .nav-toggler span {
  height: 2px;
  width: 16px;
  display: inline-block;
  position: relative;
}

.aside_left .nav-toggler span:after,
.aside_left .nav-toggler span:before {
  content: '';
  height: 2px;
  width: 18px;
  position: absolute;
  top: -6px;
  left: 0;
}

.aside_left .nav-toggler span:after {
  top: 6px;
}

.home {
  min-height: 100vh;
  display: flex;
}

.home .intro {
  text-align: center;
}

.home .intro img {
  height: 200px;
  width: 200px;
  border-radius: 50%;
  display: inline-block;
  border: 8px solid #fff;
}

.home .intro h1 {
  font-size: 30px;
  color: #302e4d;
  font-family: 'Rubik', sans-serif;
  font-weight: 700;
  margin: 20px 0 5px;
}

.home .intro p {
  font-size: 16px;
  font-weight: 500;
  margin: 0;
  line-height: 22px;
  color: #504e70;
}

.shadow-dark {
  box-shadow: 0 0 20px rgba(48, 46, 77, 0.15);
}

.social-link {
  margin-top: 25px;
}

.social-link li {
  display: inline-block;
  list-style-type: none;
}

.social-link li a {
  width: 35px;
  height: 35px;
  line-height: 35px;
  display: inline-block;
  text-align: center;
  color: #fff;
  margin: 0 4px;
  border-radius: 50%;
  text-decoration: none;
  transition: all .3s ease;
}

.social-link li a:hover {
  transform: scale(1.1);
}

.section .container {
  padding-top: 60px;
  padding-bottom: 70px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
  position: relative;
}

.padd-15 {
  padding-left: 15px;
  padding-right: 15px;
}

.section .section-title {
  max-width: 100%;
  flex: 0 0 100%;
  margin-bottom: 60px;
}

.section .section-title h2 {
  font-size: 40px;
  color: #302e4d;
  font-family: 'Rubik', sans-serif;
  font-weight: 700;
  position: relative;
  margin: 0;
}

.section .section-title h2:before {
  left: 0;
  top: 100%;
  content: '';
  height: 4px;
  width: 50px;
  position: absolute;
}

.section .section-title h2:after {
  margin-top: 8px;
  left: 0;
  top: 100%;
  content: '';
  height: 4px;
  width: 25px;
  position: absolute;
}

.hedden {
  display: none;
}

.about .about-content {
  flex: 0 0 100%;
  max-width: 100%;
}

.about .about-content .about-text {
  flex: 0 0 100%;
  max-width: 100%;
}

.about .about-content .about-text h3 {
  font-size: 24px;
  margin-bottom: 15px;
  font-weight: 700;
  columns: #302e4d;
}

.about .about-content .about-text p {
  font-size: 16px;
  line-height: 25px;
  columns: #504e70;
  margin: 0;
}

.about .about-content .personal-info {
  flex: 0 0 60%;
  max-width: 60%;
  margin-top: 40px;
}

.about .about-content .skills {
  flex: 0 0 40%;
  max-width: 40%;
}

.about .about-content .personal-info .info-item {
  flex: 0 0 50%;
  max-width: 50%;
}

.about .about-content .personal-info .info-item p {
  font-size: 16px;
  font-weight: 600;
  padding: 10px 0;
  color: #302e4d;
  border-bottom: 1px solid #e8dfec;
  margin: 0;
}

.about .about-content .personal-info .info-item p span {
  font-weight: 400;
  color: #504e70;
  margin-left: 4px;
  display: inline-block;
}

.btn {
  font-size: 16px;
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  padding: 12px 35px;
  color: #fff;
  border-radius: 40px;
  white-space: nowrap;
  display: inline-block;
  transition: all .3s ease;
}

.btn:hover {
  transform: scale(1.1);
}

.about .about-content .personal-info .buttons .btn {
  margin-right: 15px;
  margin-top: 10px;
}

.about .about-content .skills .skill-item {
  max-width: 100%;
  flex: 0 0 100%;
}

.about .about-content .skills .skill-item h5 {
  line-height: 40px;
  font-weight: 600;
  font-size: 16px;
  color: #302e4d;
  margin: 0;
  text-transform: capitalize;
}

.about .about-content .skills .skill-item .progress {
  width: 100%;
  height: 8px;
  border-radius: 4px;
  background-color: #e3e3e3;
  position: relative;
}

.about .about-content .skills .skill-item {
  margin-bottom: 30px;
}

.about .about-content .skills .skill-item .progress .progress-in {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  border-radius: 4px;
}

.about .about-content .skills .skill-item .skill-percent {
  line-height: 40px;
  position: absolute;
  right: 0;
  top: -40px;
  font-weight: 400;
}

.about .about-content .education {
  flex: 0 0 50%;
  max-width: 50%;
}

.about .about-content .experience {
  flex: 0 0 50%;
  max-width: 50%;
}

.about .about-content h3.title {
  font-size: 24px;
  margin-bottom: 40px;
  font-weight: 700;
  color: #302e4d;
}

.about .about-content .timeline-box {
  flex: 0 0 100%;
  max-width: 100%;
}

.about .about-content .timeline {
  background-color: #fdf9ff;
  padding: 30px 15px;
  border: 1px solid #d4d4e3;
  border-radius: 10px;
  width: 100%;
  position: relative;
}

.about .about-content .timeline .timeline-item {
  position: relative;
  padding-left: 37px;
  padding-bottom: 50px;
}

.about .about-content .timeline .timeline-item:before {
  content: '';
  width: 1px;
  position: absolute;
  height: 100%;
  left: 7px;
  top: 0;
}

.about .about-content .timeline .timeline-item:last-child {
  padding-bottom: 0;
}

.about .about-content .timeline .circle-dot {
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 50%;
  height: 15px;
  width: 15px;
}

.about .about-content .timeline .timeline-date {
  font-weight: 400;
  font-size: 14px;
  margin-top: -5px;
  margin-bottom: 12px;
  color: #504e70;
}

.about .about-content .timeline .timeline-date .fa {
  margin-right: 5px;
}

.about .about-content .timeline .timeline-title {
  font-weight: 700;
  font-size: 18px;
  margin-bottom: 15px;
  text-transform: capitalize;
  color: #302e4d;
}

.about .about-content .timeline .timeline-text {
  font-size: 16px;
  line-height: 25px;
  color: #504e70;
  margin: 0;
}

.service .container {
  padding-bottom: 40px;
}

.service .service-item {
  flex: 0 0 33.33%;
  max-width: 33.33%;
  margin-bottom: 30px;
  display: flex;
}

.service .service-item .service-item-inner {
  background-color: #fdf9ff;
  border: 1px solid #d4d4e3;
  border-radius: 10px;
  padding: 30px 15px;
  text-align: center;
  transition: all .3s ease;
}

.service .service-item .service-item-inner .service-item-inner:hover {
  box-shadow: 0 0 20px rgba(48, 46, 77, 0.15);
}

.service .service-item .service-item-inner .icon {
  width: 60px;
  height: 60px;
  display: block;
  margin: 0 auto 30px;
  text-align: center;
  border-radius: 50%;
  transition: all .5s ease;
}

.service .service-item .service-item-inner .icon .fa {
  font-size: 40px;
  line-height: 60px;
  transition: all .5s ease;
}

.service .service-item .service-item-inner h4 {
  font-size: 18px;
  margin-bottom: 15px;
  color: #302e4d;
  font-weight: 700;
  text-transform: capitalize;
}

.service .service-item .service-item-inner p {
  font-size: 16px;
  color: #504e70;
  line-height: 25px;
  margin: 0;
}

.service .service-item .service-item-inner:hover .icon .fa {
  font-size: 25px;
}

.portfolio .container {
  padding-bottom: 40px;
}

.portfolio .portfolio-filter {
  flex: 0 0 100%;
  max-width: 100%;
  text-align: center;
  margin-bottom: 40px;
}

.portfolio .portfolio-filter button {
  border: none;
  background-color: transparent;
  font-size: 18px;
  font-weight: 700;
  text-transform: capitalize;
  margin: 0 10px;
  color: #302e4d;
  cursor: pointer;
  border-bottom: 2px solod transparent;
  white-space: nowrap;
  padding-bottom: 2px;
  transition: ease all .3s;
}

.portfolio .portfolio-item {
  flex: 0 0 33.33%;
  max-width: 33.33%;
  margin-bottom: 30px;
}

.portfolio .portfolio-item-inner {
  border-radius: 10px;
  border: 6px solid #fdf9ff;
  overflow: hidden;
  cursor: pointer;
  position: relative;
}

.portfolio .portfolio-item-inner:hover .portfolio-info h4 {
  transform: translateX(0px);
  opacity: 1;
}

.portfolio .portfolio-item-inner .portfolio-img img {
  width: 100%;
  display: block;
}

.portfolio .portfolio-item .portfolio-info {
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100%;
  z-index: 1;
  padding: 30px;
  opacity: 0;
  transition: all .3s ease;
}

.portfolio .portfolio-item .portfolio-info h4 {
  font-size: 18px;
  font-weight: 700;
  color: #fff;
  text-transform: capitalize;
  transition: all .3s ease;
  transform: translateX(-20px);
  opacity: 0;
}

.portfolio .portfolio-item-inner:hover .portfolio-info {
  opacity: 1;
}

.portfolio .portfolio-item .portfolio-info .icon {
  height: 40px;
  width: 40px;
  background-color: #fff;
  text-align: center;
  border-radius: 50%;
  position: absolute;
  right: 30px;
  bottom: 30px;
  transition: all .3s ease;
  transform: translateX(20px);
  opacity: 0;
}

.portfolio .portfolio-item-inner:hover .portfolio-info .icon {
  transform: translateX(0px);
  opacity: 1;
}

.portfolio .portfolio-item .portfolio-info .icon .fa {
  line-height: 40px;
}

.social-link li a {
  background-color: #ec1839;
}

.portfolio .portfolio-item .portfolio-info .icon .fa,
.portfolio .portfolio-filter button.active,
.service .service-item .service-item-inner .icon .fa,
.about .about-content .about-text h3 span,
.aside_left .nav li a:hover,
.aside_left .nav li a.active {
  color: #ec1839;
}

.service .service-item .service-item-inner:hover .icon .fa {
  color: #fff;
}

.service .service-item .service-item-inner:hover .icon,
.about .about-content .timeline .circle-dot,
.about .about-content .timeline .timeline-item:before,
.about .about-content .skills .skill-item .progress .progress-in,
.btn,
.section .section-title h2:after,
.section .section-title h2:before,
.aside_left .nav-toggler span:after,
.aside_left .nav-toggler span:before,
.aside_left .nav-toggler span {
  background-color: #ec1839;
}

.portfolio .portfolio-filter button.active {
  border-color: #ec1839;
}

.logo a:after {
  border-right: 4px solid #ec1839;
  border-bottom: 4px solid #ec1839;
}

.logo a:before {
  border-top: 4px solid #ec1839;
  border-left: 4px solid #ec1839;
}

